<template>
  <b-container fluid class="mt--7">
    <b-row>
      <b-col lg="2" style="padding: 0;">
        <form-loader height_custom="260px" v-if="loading" />

        <card v-else header-classes="bg-transparent">
          <b-row class="group-filter">
            <b-col cols="6" lg="6" style="display: flex;">
              <base-button @click="initFilters" size="sm" class="filter" type="default">{{ $t('general.filter') }}
              </base-button>
              <base-button @click="eraseFilters" size="sm" class="filter" type="default">{{ $t('general.clear') }}
              </base-button>
            </b-col>
          </b-row>
          <br><br>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.products.index.number')"
                v-model="search_by_number" />
            </b-col>
          </b-row>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input input-classes="form-control-sm" type="text" :label="$t('cruds.products.index.name')"
                v-model="search_by_name" />
            </b-col>
          </b-row>

          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input :name="$t('cruds.products.index.type')" :label="$t('cruds.products.index.type')">
                <el-select v-model="search_by_type" :placeholder="$t('cruds.products.index.type')">
                  <el-option v-for="option in lists.types" :key="option.label"
                    :label="$t('cruds.products.category.' + option.label)" :value="option.value">
                  </el-option>
                </el-select>
              </base-input>
            </b-col>
          </b-row>
          <b-row class="group-filter">
            <b-col cols="12" lg="12">
              <base-input :name="$t('cruds.products.index.category_id')" :label="$t('cruds.products.index.category_id')">
                <el-select v-model="search_by_category_id" :placeholder="$t('cruds.products.index.category_id')">
                  <el-option v-for="option in lists.categories" :key="option.label" :label="option.label"
                    :value="option.value">
                  </el-option>
                </el-select>
              </base-input>
            </b-col>
          </b-row>
        </card>
      </b-col>

      <b-col lg="10">
        <form-loader height_custom="410px" v-show="loading" />

        <card v-show="!loading">
          <b-col cols="12" lg="12" class="py-2 text-right" v-if="$can('product_create')">
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_export')">
              <i class="fa-solid fa-download"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.csv_import')">
              <i class="fa-solid fa-upload"></i>
            </base-button>
            <base-button size="sm" class="btn btn-default" :title="$t('general.print')">
              <i class="fa-solid fa-print"></i>
            </base-button>
            <base-button @click="navigateToGrid" size="sm" class="btn btn-default" :title="$t('general.grid')">
              <i class="fa-solid fa-table-cells"></i>
            </base-button>
            <base-button @click="navigateToCreate" size="sm" class="btn btn-default">
              {{ $t('general.new') }}
            </base-button>
          </b-col>

          <sw-datatable :tableColumns="tableColumns" :tableData="data" :total="total" :loading="loading"
            @paginationChanged="clickPagination" @delete="deleteRow" :xprops="xprops"/>

          <div  class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap py-1">
            <p class="card-category">
              {{ total }} {{ $t('general.datatable_registers') }}
            </p>

            <base-pagination class="pagination-no-border py-0" :current="query.page" :per-page="query.limit"
              :total="total" @change="clickPagination($event)">
            </base-pagination>
          </div>
        </card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import products_filter from './mixins/products_filter'
import DatatableTranslateType from '@/components/Datatables/DatatableTranslateType.vue';
import DatatableMaskMoney from '@/components/Datatables/DatatableMaskMoney.vue';
import DatatableRelationship from '@/components/Datatables/DatatableRelationship.vue';
import DatatableImage from '@/components/Datatables/DatatableImage.vue';
import DatatableProductItem from '@/components/Datatables/DatatableProductItem.vue';

export default {
  mixins: [products_filter],
  data() {
    return {
      tableColumns: {
        'path': {
          prop: 'path',
          label: '',
          minWidth: 70,
          component: DatatableImage
        },
        'name': {
          prop: 'name',
          label: this.$t('cruds.products.index.name'),
          minWidth: 140,
          sortable: true,
          component: DatatableProductItem
        },
        'type': {
          prop: 'type',
          label: this.$t('cruds.products.index.type'),
          translatePath: 'cruds.products.category',
          minWidth: 84,
          sortable: true,
          component: DatatableTranslateType
        },
        'category.name': {
          prop: 'category.name',
          label: this.$t('cruds.products.index.category_id'),
          minWidth: 113,
          sortable: true,
          component: DatatableRelationship
        },
        'price': {
          prop: 'price',
          label: this.$t('cruds.products.index.price'),
          minWidth: 90,
          sortable: true,
          component: DatatableMaskMoney
        }
      },
      xprops: {
        module: "ProductsIndex",
        route: "products",
        permission_prefix: "product_",
        view_action: {
          show: false,
          edit: true,
          delete: true,
        },
      },
    };
  },
  mounted() {
    this.query.limit = 6
    this.initFilters(this.query);
  },
  computed: {
    ...mapGetters("ProductsIndex", ["data", "total", "lists", "loading"]),
  },
  methods: {
    ...mapActions("ProductsIndex", [
      "fetchIndexData",
      "setQuery",
      "resetState",
      "setLinearFilters",
      "fetchCategoriesByTypeData",
      "destroyData"
    ]),
    navigateToCreate() {
      this.$router.push({ name: "products.create" });
    },
    navigateToGrid() {
      this.$setViewPath('products', '/grid')
      this.$router.push({ name: "products.grid" });
    },
    deleteRow(row){
      this.destroyData(row.id)
    }
  },
};
</script>

<style scoped>
.filter {
  font-size: xx-small;
}

.group-filter {
  margin-left: -26px;
  margin-bottom: -20px;
}

.form-control-sm {
  width: 112%;
}

.form-control-label {
  color: #191c21;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 0;
}
</style>