export default {
  data() {
    return {
      query: { sort: "id", order: "desc", offset: 0, limit: 4, s: "", page: 1 },
      search_by_number: "",
      search_by_name: "",
      search_by_type: "",
      search_by_category_id: ""
    }
  },
  watch: {
    search_by_number(value) {
      this.updateLinearFilter("id", value);
    },
    search_by_name(value) {
      this.updateLinearFilter("name", value);
    },
    search_by_type(value) {
      this.updateLinearFilter("type", value);
      this.fetchCategoriesByTypeData(value)
    },
    search_by_category_id(value) {
      this.updateLinearFilter("category_id", value);
    }
  },
  methods: {
    eraseFilters() {
      this.clearFilters()
      this.query.page = 1;
      this.resetState();
      this.initFilters(this.query);
    },
    clickPagination(page) {
      this.clearFilters()
      this.query.page = page;
      this.initFilters(this.query);
    },
    initFilters(query) {
      this.setQuery(query);
      this.fetchIndexData(this.search_by_type);
    },
    updateLinearFilter(column, query_1) {
      this.setLinearFilters({
        column: column,
        query_1: query_1
      });
    },
    clearFilters() {
      this.search_by_number = "";
      this.search_by_name = "";
      this.search_by_type = "";
      this.search_by_category_id = "";
      this.updateLinearFilter("id", '');
      this.updateLinearFilter("name", '');
      this.updateLinearFilter("type", '');
      this.updateLinearFilter("category_id", '');
    }
  }
}
