<template>
  <span>
    <span>{{ row[relation][field] }}</span>
  </span>
</template>

<script>
export default {
  props: ["row", "rowOptions", "property"],
  computed: {
    property_splited() {
      return this.property.split('.')
    },
    relation() {
      return this.property_splited[0]
    },
    field() {
      return this.property_splited[1]
    }
  }
};
</script>
